.user-analytics {
  max-width: 900px;
}

.user-analytics h2 {
  color: #333;
  margin-bottom: 30px;
  font-size: 1.8rem;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.stat-card {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 25px;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.stat-card.donation {
  background: linear-gradient(135deg, #28a745 0%, #20c997 100%);
}

.stat-card.loss {
  background: linear-gradient(135deg, #dc3545 0%, #c82333 100%);
}

.stat-icon {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.stat-value {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 0.9rem;
  opacity: 0.9;
}

.book-distribution-section {
  background: #f8f9fa;
  padding: 25px;
  border-radius: 12px;
}

.book-distribution-section h3 {
  color: #333;
  margin-bottom: 20px;
  font-size: 1.3rem;
}

.book-list {
  display: grid;
  gap: 15px;
}

.book-item {
  background: white;
  padding: 20px;
  border-radius: 8px;
  border: 2px solid #e0e0e0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.3s;
}

.book-item:hover {
  border-color: #667eea;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.2);
}

.book-name {
  font-weight: 600;
  color: #333;
  font-size: 1.1rem;
}

.book-count,
.book-points {
  color: #666;
  font-size: 0.95rem;
}

.loading,
.error,
.no-data {
  text-align: center;
  padding: 40px;
  color: #666;
  font-size: 1.1rem;
}

@media (max-width: 768px) {
  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }

  .stat-card {
    padding: 20px;
  }

  .stat-value {
    font-size: 1.5rem;
  }

  .book-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }
}

